.P-horizontalLine {
    margin: 0.3rem 0.1rem;
    background-color: var(--default-color);
    width: 100%;
    height: 0.1rem;
}

.P2-horizontalLine {
    margin: 0.1rem 3rem;
    background-color: var(--default-color);
    width: 98%;
    height: 1px;
}

.redteamicon {
    content: url("https://xivapi.com/i/061000/061378_hr1.png");
    background-size: cover;
}

.blueteamicon {
    content: url("https://xivapi.com/i/061000/061379_hr1.png");
    background-size: cover;
}

.generalteamicon {
    content: url("https://xivapi.com/i/061000/061480_hr1.png");
    background-size: cover;
}

.redteam {
    background: linear-gradient(to right, rgba(180, 56, 27, 0.5) 5%, 20%, rgba(180, 56, 27, 0.7));
}

.blueteam {
    background: linear-gradient(to right, rgba(27, 56, 180, 0.5) 5%, 20%, rgba(27, 56, 180, 0.7));
}

.generalteam {
    background: linear-gradient(to right, rgba(65, 65, 65, 0.5) 5%, 20%, rgba(65, 65, 65, 0.7));
}

/* role color*/
.role-background-tank {
    background-color: var(--transparent-tank-role-color) !important;
}

.role-background-healer {
    background-color: var(--transparent-healer-role-color) !important;
}

.role-background-melee {
    background-color: var(--transparent-melee-role-color) !important;
}

.role-background-physical {
    background-color: var(--transparent-physical-role-color) !important;
}

.role-background-magical {
    background-color: var(--transparent-magical-role-color) !important;
}

.role-background-general {
    background-color: var(--transparent-general-role-color) !important;
}

.alliance-background-gradient-1 {
    background: linear-gradient(90deg, var(--alliance-transparent-color-1) 0%, var(--alliance-transparent-color-1) 98%, transparent 100%);
}

.alliance-background-gradient-2 {
    background: linear-gradient(90deg, var(--alliance-transparent-color-2) 0%, var(--alliance-transparent-color-2) 98%, transparent 100%);
}

.alliance-background-gradient-3 {
    background: linear-gradient(90deg, var(--alliance-transparent-color-3) 0%, var(--alliance-transparent-color-3) 98%, transparent 100%);
}

.alliance-background-gradient-4 {
    background: linear-gradient(90deg, var(--alliance-transparent-color-4) 0%, var(--alliance-transparent-color-4) 98%, transparent 100%);
}

.alliance-background-gradient-5 {
    background: linear-gradient(90deg, var(--alliance-transparent-color-5) 0%, var(--alliance-transparent-color-5) 98%, transparent 100%);
}

.alliance-background-gradient-6 {
    background: linear-gradient(90deg, var(--alliance-transparent-color-6) 0%, var(--alliance-transparent-color-6) 98%, transparent 100%);
}

