.tooltip-assistKDA {
    display: grid;
    align-items: center;
    grid-row-gap: 0.1rem;
    justify-items: center;
}
.tooltip-assist {
    grid-template-columns: 1.2rem auto 1rem 1.2rem auto 1.5rem auto;
}
.tooltip-kill {
    grid-template-columns:1.2rem auto 1.5rem auto;
}
.tooltip-death {
    grid-template-columns: 1.2rem auto 1.5rem auto;
}
.tooltip-assistKDA .iconSize ,.tooltip-jobhistory .iconSize{
    font-size: .8rem;
    padding: 0 0.2rem;
}

.tooltip_general {
    font-size: .65rem!important;
    padding: 0.3rem 0.8em!important;
    z-index: 1;
}

.tooltip-jobhistory {
    display: grid;
    align-items: center;
    grid-row-gap: 0.1rem;
    justify-items: center;
    grid-template-columns: 1.2rem 1rem 1.2rem 1.5rem auto;
}

.tooltip-limitbreak {
    display: grid;
    align-items: center;
    grid-row-gap: 0.1rem;
    justify-items: center;
    grid-template-columns: 1.2rem 2.2rem 1fr 2.2rem 2.2rem;
}
.tooltip-portion {
    display: grid;
    align-items: center;
    grid-row-gap: 0.1rem;
    justify-items: center;
    grid-template-columns: 1.2rem auto 3rem 2rem;
}
.tooltip-dynamishistory {
    display: grid;
    align-items: center;
    grid-row-gap: 0.1rem;
    justify-items: center;
    grid-template-columns: 1rem 0.8rem 1rem 1.5rem auto;
}
.tooltip_png_iconSize {
    width: 0.8rem;
}
.tooltip-personTooltip {
    display: flex;
    min-width: 8rem;
    justify-content: space-between;
}
.tooltip-damage-opt{
    display: flex;
    align-items: center;
    gap:0.2rem 0.3rem;
}
.tooltip-punch {
    display: grid;
    justify-items: center;
    grid-template-columns: auto 0.6rem auto;
}
.tooltip-damage-cc {
    display: grid;
    align-items: center;
    grid-template-columns: 0.1rem 2rem auto 2rem 1.2rem;
    justify-items: center;
}